.btn {
  background-color: white;
  color: #007bff;
  padding: 1rem 1.7rem;
}
.btn--rounded {
  border-radius: 2rem;
}

.btn-block {
  display: block;
}

.color-black {
  color: black;
}

.text-center {
  text-align: center;
}

.btn-login {
  display: block;
  width: 100%;
  border-radius: 5px;
  background-color: #b3d8ff;
  color: white;
  padding-top: 1.1rem;
  padding-bottom: 1.1rem;
  border: none;
}

.btn-social {
  display: block;
  width: 100%;
  border-radius: 5px;
  background-color: #b3d8ff;
  color: white;
  padding-top: 0.4rem;
  padding-bottom: 0.4rem;
  border: none;
  font-size: 1.3rem;
  margin-bottom: 1rem;
}

.blueLight {
  background-color: #4387f4;
}

.blueStrong {
  background-color: #2e5c99;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-size: 65.2%;
}

*,
*:after,
*::before {
  box-sizing: inherit;
}

/* body */
body {
  line-height: 4rem;
  font-size: 1.6rem;
  background-color: rgba(111, 83, 175, 0.782);
  color: white;
  font-family: "Poppins", sans-serif;
  height: 100vh;
}

/******************************************Global Styles**********************************/
.text-center {
  text-align: center;
}

p {
  line-height: 1.6;
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
  color: white;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: inline-block;
}

header {
  background-color: #007bff;
  height: 100vh;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 8rem;
  padding: 0 0.6rem;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}
.nav .logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  width: 12rem;
}
@media only screen and (min-width: 1024px) {
  .nav .logo {
    width: 13rem;
  }
}
.nav .logo .svg-icon {
  width: 3.5rem;
}
@media only screen and (min-width: 1024px) {
  .nav .logo .svg-icon {
    width: 5rem;
  }
}
.nav .container-list {
  display: none;
  height: 100%;
  justify-content: space-between;
  align-items: center;
  flex: 0 0 29rem;
}
@media only screen and (min-width: 1024px) {
  .nav .container-list {
    display: flex;
  }
}

.login-section {
  display: flex;
  height: calc(100vh - 8rem);
  align-items: center;
  justify-content: center;
}
.login-section .login {
  width: 36rem;
  height: 50rem;
  background-color: white;
  padding: 0rem 3rem;
  border-radius: 1rem;
}
.login-section .login__title {
  color: black;
  font-weight: 400;
}
.login-section .login .formulary input {
  display: block;
  width: 100%;
  padding: 1.9rem 0.5rem;
  border-radius: 5px;
  margin-bottom: 1.2rem;
  background-color: #eef3fb;
  font-size: 1.2rem;
  padding-left: 1.2rem;
}
.login-section .login .formulary .password-link {
  font-size: 1.2rem;
  color: #007bff;
}
.login-section .login .link-account {
  font-size: 1.3rem;
  color: black;
}
.login-section .login .link-account .signUp {
  color: #e9901c;
  text-decoration: underline;
}